.home {
    min-height: 100vh;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

.home .homeIntro {
    display: flex;
    margin-top: 7%;
}

.home .tigerDiv {
    height: 250px;
    width: auto;
}

.home .homeDiv {
    color: #977e56;
    text-align: left;
    margin-left: 20%;
    width: 40%;
    font-family: 'Open Sans', sans-serif;
    font-size: 2rem;
}

.home .homeDiv > p {
    padding: 0.5rem;
}

.home .arrowButton {
    text-align: center;
    margin-top: 2%;
}

.home .arrowDiv {
    height: 150px;
    width: auto;
    margin-top: 3rem;
}

.home .arrowDiv:hover {
    background-color: rgb(168 140 96 / 15%);
    transform: scale(1.1);
    border-radius: 50%;
}

@media screen and (max-width: 768px) {
    .home .homeIntro {
        display: block;
    }

    .home .homeIntro .homeDiv {
        width: 60%;
    }

    .home .homeIntro .tigerDiv {
        margin-top: 5%;
    }
}

@media screen and (max-width: 480px) {
    .home .homeIntro .homeDiv {
        margin-left: 10%;
        margin-top: 15%;
        width: 80%;
        font-family: 'Open Sans', sans-serif;
        font-size: 1.5rem;
    }
    .home .homeIntro .tigerDiv {
        height: 200px;
        position: absolute;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 80%;
        left: 0;
        right: 0;
        bottom: 0;
        text-align: center;
    }
    .home .arrowButton {
        position: absolute;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 25%;
        left: 0;
        right: 0;
        bottom: 0;
        text-align: center;
    }
}
